.load-page-content {
  --padding-top: 20px !important; }

.load_details_header {
  text-align: center; }

.ion-card ion-card-header {
  padding-left: 11px;
  padding-right: 20px; }

.ion-card .accordion-header {
  height: 50px;
  padding-top: 15px; }
  .ion-card .accordion-header span {
    font-size: 16px;
    color: #3e3e3e;
    font-family: "Roboto Medium"; }
  .ion-card .accordion-header .up {
    transform: rotate(-135deg);
    -webkit-transform: rotate(-135deg); }
  .ion-card .accordion-header .down {
    transform: rotate(45deg);
    -webkit-transform: rotate(45deg); }
  .ion-card .accordion-header i {
    border: 1px solid #FF6418;
    border-width: 0 1.5px 1.5px 0;
    display: inline-block;
    padding: 4px;
    margin-top: 3px;
    float: right; }

.ion-card ion-card-content .load_details_grid {
  padding: 0; }
  .ion-card ion-card-content .load_details_grid ion-col {
